body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-container {
  position: relative;
  height: 300px;
}

.header-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-size: cover;
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}

h2 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-align: center;
  text-transform: uppercase;
}


.header-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.logo-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
}

h1 {
  font-size: 3rem;
}

/* Define variables */
:root {
  --primary-color: #3498db;
  --secondary-color: #2ecc71;
  --background-image: url('https://example.com/background-image.jpg');
}

/* Global styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
}

/* Action list styles */
.action-list-container {
  background-image: url('https://example.com/background-image.jpg');
  background-image: var(--background-image);
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.action-list {
  /* background-color: rgba(255, 255, 255, 0.8); */
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  background-color: #f5f5dc;
}

.instructions {
  text-align: center;
  font-family: cursive;
  margin-bottom: 20px;
}

.instructions h1 {
  font-size: 36px;
  margin-bottom: 10px;
}

.instructions p {
  font-size: 20px;
}

.action-container {
  text-align: center;
}

.action-container h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

.action {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.action:hover {
  background-color: #f5deb3; 
  border: 1px solid #8b4513;
  box-shadow: 2px 2px #8b4513; 
  text-decoration: underline; 
  }

  .action.completed {
    background-color: #ccc;
    border: 1px solid #ccc;
  }
  
  
.checkmark {
  margin-right: 10px;
  font-size: 20px;
  color: #2ecc71;
  color: var(--secondary-color);
}

.action-text {
  font-size: 18px;
  font-weight: bold;
}

.score-container {
  text-align: center;
  margin-top: 20px;
}

.score-container button {
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #3498db;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.score-container button:hover {
  background-color: #2980b9;
}

.score-container p {
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
}

.instructions p {
  padding-bottom: 30px;
}

.final-score {
  background-color: #f7f7f7;
  border-radius: 8px;
  padding: 32px;
  text-align: center;
  font-family: sans-serif;
}

.final-score__image {
  max-width: 100%;
  margin-bottom: 16px;
}

.final-score__heading {
  font-size: 32px;
  margin-bottom: 16px;
}

.final-score__text {
  font-size: 24px;
  margin-bottom: 32px;
}

.final-score__button {
  background-color: #1da1f2;
  border: none;
  border-radius: 8px;
  color: white;
  padding: 16px 32px;
  font-size: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in-out;
}

.final-score__button:hover {
  background-color: #0d7bbd;
}

.final-score__button-icon {
  width: 32px;
  height: 32px;
  margin-right: 16px;
}

